$green: #9bc1bc

.square
	width: 13px
	height: 13px
	background: $green
	display: inline-block

.text-big
	font-size: 24px
	+med
	&_md
		font-size: 20px
	+r3
		font-size: 20px
		&_md
			font-size: 18px

.section-title
	font-size: 18px
	letter-spacing: 2.6px
	color: $green
	margin-bottom: 20px

.meta
	display: inline-block
	+reg
	font-size: 11px
	color: #b7b7b7
	+r2
		display: block

.divider
	height: 1px
	margin: 20px 0
	background: $green

// news
.l-news
	position: relative
	padding: 0 110px
	+clr
	.l-col1
		float: left
		width: 70%
	.l-col2
		float: right
		padding-left: 30px
		width: 30%
	+r1
		padding-right: 0
		.l-col1,
		.l-col2
			width: 100%
			float: none
		.l-col2
			padding-left: 0
		.banner-img
			display: none
	+r2
		padding-left: 0

// soc-v
.soc-v
	position: absolute
	left: 0
	top: 0
	> li
		text-align: center
		margin-bottom: 1px
		display: block
		a
			font-size: 29px
			color: #fff
			padding: 12px 10px
			background: #9bc1bc
			width: 50px
			height: 50px
			+tr(background .3s)
			&.fa-envelope
				font-size: 24px
			&.icon-vk
				font-size: 24px
			&:hover
				background: darken(#9bc1bc, 10)
	+r2
		position: static
		+clr
		> li
			float: left
			margin-right: 1px

// news
.news-header
	&__subtitle
		margin-bottom: 10px
		font-size: 30px
		color: #9bc1bc
		+bold
	h1, .h1
		margin-bottom: 20px
	.text-big
		margin-bottom: 40px

.news-line
	margin: 0 -5px 40px
	.btn
		display: inline-block
		vertical-align: middle
		margin-right: 20px
		margin-left: -5px
	&__info
		display: inline-block
		max-width: 80%
		vertical-align: middle
		color: #757575
		span
			display: inline-block
			padding: 0 5px
			+light
	+r3
		&__info
			display: block
			margin-top: 10px
		.btn
			margin-left: 0

// sidebar
.banner-img
	padding: 50px 25px
	border: 1px solid #e6e6e6
	text-align: center
	margin-bottom: 20px
	background: #fff
	a
		line-height: 0
	img
		max-width: 100%
	+r2
		display: none

.item-sm
	font-size: 16px
	padding-bottom: 15px
	margin-bottom: 15px
	border-bottom: 1px solid #e6e6e6
	&:last-child
		border: 0
	+light
	p
		line-height: 1.2
	.text-big
		margin-bottom: 10px
		line-height: 1
	&:hover
		.text-big
			text-decoration: underline

.disqus
	display: block
	max-width: 100%

.footnote
	padding-top: 20px
	text-align: center
	border-top: 4px solid #9bc1bc
	&_mod
		margin-left: 108px
	span
		color: #9BC1BC
	+r2
		margin-left: 80px
	+r3
		margin-left: 0

.download
	display: block
	margin: 27px 0 35px
	padding: 13px 22px
	font-size: 18px
	color: #000
	border: 1px solid #9BC1BC
	&_mod
		margin-left: 108px
	.fa
		margin-right: 18px
		font-size: 24px
		color: #9BC1BC
		vertical-align: middle
	span
		vertical-align: middle
	+r2
		margin-left: 80px
	+r3
		margin-left: 0
		padding: 13px
		text-align: center
		span
			display: block