.item-sm:last-child { border: 0; }
.item-sm p { line-height: 1.2; }
.item-sm .text-big { margin-bottom: 10px; line-height: 1; }
.item-sm:hover .text-big { text-decoration: underline; }

.disqus { display: block; max-width: 100%; }

.footnote { padding-top: 20px; text-align: center; border-top: 4px solid #9bc1bc; }
.footnote_mod { margin-left: 108px; }
.footnote span { color: #9BC1BC; }
@media only screen and (max-width: 990px) { .footnote { margin-left: 80px; } }
@media only screen and (max-width: 660px) { .footnote { margin-left: 0; } }

.download { display: block; margin: 27px 0 35px; padding: 13px 22px; font-size: 18px; color: #000; border: 1px solid #9BC1BC; }
.download_mod { margin-left: 108px; }
.download .fa { margin-right: 18px; font-size: 24px; color: #9BC1BC; vertical-align: middle; }
.download span { vertical-align: middle; }
@media only screen and (max-width: 990px) { .download { margin-left: 80px; } }
@media only screen and (max-width: 660px) { .download { margin-left: 0; padding: 13px; text-align: center; }
  .download span { display: block; } }


