// # SHORT HOWTO
// ## Sprites
//     .whatever
// 			+s(codedby)
// ## gradient example
//     	+g(#232323, #5c5c5c)
//	   horizontal
//     	+gh(#232323, #5c5c5c)
// ## making bw-images
//     img
//			@extend %gray
// ## predefined media-queries: https://github.com/paranoida/sass-mediaqueries
//     +r(320)
//			display: none
// +max-screen(1000), +iphone5, +iphone4, +ipad-retina, +ipad, +hdpi - also available
//
// ## use cycles
//     @for $i from 1 through 3
//       .item-#{$i}
//			width: 2em * $i
//     @each $s in a,b,c
// 	    	#{$s}
//				display: none
// ## fonts
// Save fonts to /src/fonts
// +font(book, cytiapro-black-webfont)
// and create mixin
// =book
// 	font-family: "book", Arial, sans-serif

// media
=r1
	@media only screen and (max-width: 1320px)
		@content

=r2
	@media only screen and (max-width: 990px)
		@content
=r3
	@media only screen and (max-width: 660px)
		@content
=r4
	@media only screen and (max-width: 480px)
		@content

// fonts

// roboto-font
=roboto
	font-family: 'Roboto', Arial, sans-serif

// lora-font
=lora
	font-family: 'Lora', serif
	font-weight: 400
=lorai
	font-family: 'Lora', serif
	font-weight: 400
	font-style: italic
=lorab
	font-family: 'Lora', serif
	font-weight: 700
=lorabi
	font-family: 'Lora', serif
	font-weight: 700
	font-style: italic

// 'Fira Sans' font
=reg
	font-family: 'Fira Sans', Helvetica, Arial, sans-serif
	font-weight: 400

=regi
	font-weight: 400
	font-style: italic

=light
	font-weight: 300

=med
	font-weight: 500

=medi
	font-weight: 500
	font-style: italic

=bold
	font-weight: 700

=boldi
	font-weight: 700
	font-style: italic

body
	min-width: 320px
	font-size: 14px
	color: #282828
	line-height: 1.4
	+reg
	&.body_bg-grey
		background: #f4f5f1

// colors
$violet: #c0a6cd
$gold: #f2c05e
$red: #d27857
$orange: #ff8c61
$mint: #9bc1bc

// common blocks
.container
	max-width: 1320px
	padding: 0 15px
	margin: 0 auto
	&_search
		padding: 40px 15px
	&_news
		padding: 90px 15px 20px
	+r1
		max-width: 990px
	+r2
		max-width: 660px
		padding-top: 20px
	+r3
		width: 100%

.l-center
	max-width: 1070px
	margin: 0 auto

.tech-row
	padding: 10px 0

.row
	font-size: 0
	margin: 0 -15px

.col-lg, .col-sm, .col-xs
	display: inline-block
	padding: 0 15px
	vertical-align: top

.col-lg
	width: 75%
	+r1
		width: 66.66%
	+r2
		width: 100%

.col-sm
	width: 33.33%
	+r2
		width: 50%
	+r3
		width: 100%

.col-xs
	width: 25%
	+r1
		width: 33.33%
	+r2
		width: 50%
	+r3
		width: 100%

strong, b
	font-weight: 400

i, em
	font-style: normal

h1, .h1
	font-size: 60px
	+bold
	line-height: 1.06
	+r3
		font-size: 32px

.h2
	font-size: 45px
	color: #0e0e0e
	line-height: 1.5
	+light
	&_mb
		margin-bottom: 20px
	+r3
		font-size: 40px
a
	color: #000
	text-decoration: none

// buttons
.btn
	display: inline-block
	text-transform: uppercase
	color: #282828
	position: relative
	padding: 8px 18px 7px
	font-size: 11px
	z-index: 1
	+tr(color .3s)
	&_bd
		border: 1px solid #333
	&:before
		content: ''
		position: absolute
		left: 0
		top: 0
		bottom: 0
		width: 5px
		background: #333
		z-index: -1
		+tr(width .3s)
	&:hover
		color: #fff
		&:before
			width: 100%
	&_gold
		&.btn_bd
			border-color: $gold
		&:before
			background-color: $gold
	&_orange
		&.btn_bd
			border-color: $orange
		&:before
			background-color: $orange
	&_red
		&.btn_bd
			border-color: $red
		&:before
			background-color: $red
	&_violet
		&.btn_bd
			border-color: $violet
		&:before
			background-color: $violet

.button
	font-size: 18px
	color: #fff
	width: 210px
	background-color: #f7b153
	padding: 10px 10px 9px
	cursor: pointer
	+tr(background .3s)
	&:hover
		background: darken(#f7b153, 10)

// item
.item
	position: relative
	margin-bottom: 30px
	height: 462px
	background: #FFF
	border: 1px solid #000
	&:hover
		&:not(.item_mod)
			.item__txt
				height: 235px
			.item__info
				opacity: 1
			.item__img
				&:before
					opacity: 1
					visibility: visible
	&_gold
		border-color: $gold
		&:hover
			&:not(.item_mod)
				.item__author,
				.item__txt
					color: $gold
		.item__instruction
			color: $gold
		.item__img
			&:before
				background: rgba(242,192,94,0.80)
		.item__txt
			&:hover
				color: $gold
	&_red
		border-color: $red
		&:hover
			&:not(.item_mod)
				.item__author,
				.item__txt
					color: $red
		.item__instruction
			color: $red
		.item__img
			&:before
				background: rgba(210,120,87,0.80)
		.item__txt
			&:hover
				color: $red
	&_violet
		border-color: $violet
		&:hover
			&:not(.item_mod)
				.item__author,
				.item__txt
					color: $violet
		.item__instruction
			color: $violet
		.item__img
			&:before
				background: rgba(142, 119, 154, 0.5)
		.item__txt
			&:hover
				color: $violet
	&_violet
		border-color: $orange
		&:hover
			&:not(.item_mod)
				.item__author,
				.item__txt
					color: $orange
		.item__instruction
			color: $orange
		.item__img
			&:before
				background: rgba(#ff8c61, 0.5)
		.item__txt
			&:hover
				color: $orange
	&_mod
		.item__img
			height: 310px
			&:before
				opacity: 0
			&:after
				opacity: 0.8
				visibility: visible
			&:hover
				&:before
					opacity: 1
					visibility: visible
				&:after
					opacity: 0
					visibility: hidden
				.item__author
					color: #fff
				.item__info
					max-height: 120px
					opacity: 1
		.item__txt
			height: 150px
			font-size: 22px
			+lora
			&:hover
				.item__instruction
					text-decoration: none
		.item__author
			color: #ccc
		.item__info
			max-height: 0
			padding: 0 15px
			color: #FFF
			overflow: hidden
		&.item_gold
			.item__img:hover
				&:before
					background: rgba(242,192,94,0.80)
		&.item_red
			.item__img:hover
				&:before
					background: rgba(210,120,87,0.80)
		&.item_violet
			.item__img:hover
				&:before
					background: rgba(142, 119, 154, 0.6)
	&_big
		height: 672px
		.item__txt
			padding-top: 0
		.item__img
			height: 510px
		.item__name
			font-size: 56px
	&__in
		display: block
	&__img
		position: relative
		display: block
		height: 300px
		background: #FFF
		z-index: 0
		+cover
		&:before,
		&:after
			content: ""
			position: absolute
			top: 0
			left: 0
			right: 0
			bottom: 0
			opacity: 0
			visibility: hidden
			+tr(0.25s)
		&:after
			background: rgba(89,52,79,0.50)
			z-index: -1
			+g(rgba(47, 28, 42, 0.5), rgba(45, 26, 40, 0.76))
	&__txt
		position: absolute
		display: block
		left: 0
		right: 0
		bottom: 0
		height: 160px
		padding: 12px 35px
		text-align: center
		font-size: 18px
		background: #FFF
		overflow: hidden
		line-height: 24px
		transition: color 0.2s ease, height 0.3s ease
		+light
		&-in
			height: 100%
			overflow: hidden
	&__info
		opacity: 0
		transition: opacity 0.2s ease, max-height 0.3s ease
	&__author
		max-width: 100%
		font-size: 14px
		color: #909090
		overflow: hidden
		text-overflow: ellipsis
		white-space: nowrap
		+tr(color 0.2s)
		+light
	&__instruction
		display: inline-block
		text-decoration: underline
		font-size: 14px
		+regi
	&__title
		position: absolute
		bottom: 20px
		left: 0
		right: 0
		text-align: center
		font-size: 18px
		.item__subtitle
			font-size: 28px
			color: #FFF
			+bold
	&__subtitle
		max-height: 84px
		margin: 5px 0
		font-size: 28px
		line-height: 28px
		overflow: hidden
		+med
	&__arr
		position: absolute
		height: 20px
		bottom: -1px
		left: 0
		right: 0
		overflow: hidden
		transform: translate3d(0,0,0)
		&:before,
		&:after
			content: ""
			position: absolute
			border-width: 10px
			border-color: transparent transparent #fff
			border-style: solid
		&:before
			left: -20px
			right: 50%
		&:after
			right: -20px
			left: 50%
	.btn
		position: absolute
		top: 10px
		left: -5px
		background: #FFF
	.man
		margin: 15px auto
		max-width: 460px
	+r1
		height: 402px
		.item__img
			height: 232px
		.item__subtitle
			font-size: 24px
			line-height: 27px
		.item__txt
			padding: 12px
		.item__txt,
		.item__title
			font-size: 15px
			line-height: 20px
		&.item_mod
			.item__img
				height: 242px
			.item__txt
				font-size: 20px
				line-height: 23px
		&.item_big
			height: 672px
			.item__img
				height: 460px
			.item__txt
				height: 210px
	+r2
		&.item_mod
			.item__img:hover
				.item__info
					max-height: 100px
	+r3
		max-width: 320px
		margin: 0 auto 30px
		height: 370px
		.item__img
			height: 222px
		.item__txt
			height: 150px
		&.item_mod
			.item__img
				height: 217px
				&:hover
					.item__info
						max-height: 80px
			.item__txt
				font-size: 19px
		&.item_big
			height: 370px
			.item__txt
				position: static
				height: auto
		.man
			display: none

.man
	text-align: left
	display: table
	&__text
		display: table-cell
		vertical-align: middle
		+light
		font-size: 16px
		line-height: 1.125
		text-align: left
		padding-left: 20px
	&__img
		display: table-cell
		vertical-align: middle
		width: 60px
		height: 60px
		border-radius: 50%
		overflow: hidden
		img
			max-width: 100%